import "./report.scss"
import React, {useState} from 'react'


export default function Report() {


    const [ Win_width, setWin_width ] = useState( document.documentElement.clientWidth )
    window.addEventListener( "resize", () => setWin_width( document.documentElement.clientWidth ) )


    return (
        Win_width > 560 ? (
        <table className="report--table">
            <thead>
                <tr>
                    <th scope="col">#</th>
                    <th scope="col">Получатель платежа</th>
                    <th scope="col">Дата</th>
                    <th scope="col">Сумма</th>
                    <th scope="col">Описание</th>
                    <th scope="col">Крайняя дата</th>
                    <th scope="col">ИП/Касса</th>
                    <th scope="col">Статус платежа</th>
                    <th scope="col">Статья расходов</th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <th scope="row">1</th>
                    <td>Ma</td>
                </tr>
                <tr>
                    <th scope="row">2</th>
                </tr>
            </tbody>
        </table>
        ):
        (
            <form action='http://file'>
            <input 
            style={{cursor: "pointer", marginLeft: "2rem", marginBottom: "3rem", border: "2px solid #85c469"}}
                className="btn btn-secondary"
                type='submit' 
                value='Скачать таблицу'/>
            </form>
        )
    )
}