import React, {useState, useRef} from 'react'
import "./_selected.scss"


export default function InputFromRequest( props ) {

    const {data, setMaskString = Function.prototype} = props
    const [ isOpenSelect, setIsOpenSelect ] = useState( false )
    const inputFormRequestRef = useRef()

    const [ Win_width, setWin_width ] = useState( document.documentElement.clientWidth )
    window.addEventListener( "resize", () => setWin_width( document.documentElement.clientWidth ) )

    return (
        <div
            className={isOpenSelect ? " selectric-wrapper selectric-js-select-time selectric-belowselectric-open selectric-focus " : "selectric-wrapper selectric-js-select-time selectric-below"} >
            <div
                className="inputFormRequest">
                <input
                    key="input"
                    ref={inputFormRequestRef}
                    type="text"
                    className="input"
                    placeholder="Запрос..."
                    name="inputFormRequest"
                    onChange={() => setMaskString( inputFormRequestRef.current.value )}
                />
                <b
                    onClick={() => setIsOpenSelect( !isOpenSelect )}
                    className="button">▾</b >
            </div>
                {isOpenSelect && 
                <div
                    className="selectric-items"
                    style={{display: "block", zIndex: "29", width: "100%"}} >
                    <div
                        className="selectric-scroll" style={{height: "3rem"}}>
                        <ul>
                            {data.map( ( item, index ) => (
                                < li
                                    key={"li" + index}
                                    data-index={index + 1}
                                    onClick={( e ) => {
                                        inputFormRequestRef.current.value += `${item.data}, `
                                        setMaskString( inputFormRequestRef.current.value )
                                    }}
                                >
                                    {item.data}
                                </li>
                            ) )}
                        </ul>
                    </div>
                </div>
            }
        </div>
    )
}